import { useState } from 'react';
import { Image } from 'antd';

const ImageList = props => {

  const [ current, setCurrent ] = useState();

  const { title, description } = current ? current.directus_files_id : {};

  return (
    <>
      { props.images.map(img => 
        <Image
          key={img.id} 
          width={200} 
          src={`${process.env.REACT_APP_API_URL}/assets/${img.directus_files_id.id}?width=200&height=200`}
          preview={{ 
            src: `${process.env.REACT_APP_API_URL}/assets/${img.directus_files_id.id}?width=1200`,
            onVisibleChange: visible => visible ? setCurrent(img) : setCurrent()
          }} />
      )}

      { (title || description) && 
        <div className="image-caption-wrapper">
          <div className="image-caption">
            { title && <h4>{ title }</h4> }
            { description && <p>{ description }</p>}
          </div>
        </div>
      }
    </>
  )
  
}

export default ImageList;